/* Estilos generales para el contenedor de productos guardados */
.saved-products-container {
  padding: 20px;
  font-family: Arial, sans-serif;
}

/* Título de la página */
.page-title {
  font-size: 2rem;
  color: #333;
  margin-bottom: 20px;
}

/* Estilo para cada producto en la lista */
.product-item {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  padding: 20px;
  margin: 10px 0;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
}

.product-title {
  font-size: 1.5rem;
  color: #333;
}

.product-id {
  color: #666;
  margin-top: 10px;
}

/* Título de plantillas asignadas */
.templates-title {
  font-size: 1.2rem;
  margin-top: 15px;
  color: #444;
}

/* Estilos para la lista de plantillas */
.template-list {
  list-style-type: none;
  padding: 0;
  margin-top: 10px;
}

.template-item {
  background-color: #f1f1f1;
  margin: 5px 0;
  padding: 10px;
  border-radius: 5px;
}

.delete-template-btn {
  background-color: #e74c3c;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 10px;
  transition: background-color 0.3s ease;
}

.delete-template-btn:hover {
  background-color: #c0392b;
}

/* Contenedor para agregar una plantilla */
.add-template-container {
  margin-top: 20px;
}

.template-id-input {
  padding: 8px;
  font-size: 1rem;
  width: 60%;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.add-template-btn {
  background-color: #28a745;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-left: 10px;
}

.add-template-btn:hover {
  background-color: #218838;
}

/* Estilos de carga y error */
.loading-text,
.error-text {
  font-size: 1.2rem;
  text-align: center;
  color: #f00;
}

.loading-text {
  font-style: italic;
}

.error-text {
  color: #cc0000;
}
  